* COMMON */
body {
    margin: 0;
    font-family: 'OpenSans';
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100%;
}
  
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',monospace;
}  

html{
    text-align: center;
}

#root{
    height: inherit;
}
/* COMMON END */

/* NAVBAR */
.navbar-container{
    background-color: white;
    box-shadow: 1px 1px 5px lightgrey;
}

.navbar-container > div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 0;
}
.navbar-container > div ul{
    display: flex;
    margin: 0;
}
.navbar-container > div li{
    list-style-type: none;
    margin: 0 10px;
    cursor: pointer;
}
.navbar-container > div a{
    color: black;
}
.navbar-container img{
    width: 80px;
}

.logo {
    width: 120px;
    height: 31px;
    background: rgba(255, 255, 255, 0.2);
    margin: 16px 24px 16px 0;
    float: left;
}
/* NAVBAR END*/

/* LAYOUT */
.ant-layout{
  height: inherit;
}

.ant-layout-content{
  background-color: white;
}

.padding-container{
  padding: 1rem 2rem;
  height: 100%;
}

.language-fixed{
  position: fixed;
  top: 1rem;
  right: 1rem;
  z-index: 999;
  display: flex;
}
.language-fixed p{
  color: white;
  margin-left: 1rem;
  cursor: pointer;
}
.language-fixed p:hover{
  color: orangered;
}
/* LAYOUT END */

/* AUTH */
.auth-container{
  width: 100%;
  height: 100%;
  position: relative;
  background-color: #f3f3f3;
}

.auth-left-content{
  width: 35%;
  height: inherit;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 3rem;
  text-align: left;
}
.auth-left-content h2{
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 600;
  color: #090979;
}
.auth-left-content h3{
  font-size: 2rem;
  color: #090979;
}

.auth-logo{
  width: 30%;
  height: inherit;
  position: absolute;
  top: 0;
  left: 0;
}
.auth-logo img{
  width: 65%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
}

.auth-inputs{
  width: 65%;
  height: inherit;
  min-width: 320px;
  background: linear-gradient(99deg, #254268 30%,#727171 100%);
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem 2rem;
  box-shadow: 0px 0px 11px 0px black;
}
.auth-inputs input, .auth-inputs > span{
  margin: 5px 0;
  max-width: 400px;
  padding: 0.5rem 1rem;
  text-align: center;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}
.auth-inputs h2{
  color: white;
  font-size: 2rem;
  font-weight: 700;
}
.auth-inputs button{
  background-color: #ffffff;
  margin-top: 1rem;
  padding: 1.5rem 3rem;
  display: flex;
  align-items: center;
  color: #223354;
}
.auth-inputs span{
  font-size: 1.2rem;
}
/* AUTH END */